//  
const openMenuBtn = document.querySelector('.open-menu-btn');
const closeMenuBtn = document.querySelector('.mobile-menu-close');
const mobileMenu = document.querySelector('.mobile-menu');
const mobileMenuLinks = document.querySelectorAll('.menu-mobile-item a'); //    <a>  

//   
function openMenu() {
    mobileMenu.classList.add('is-open');
    document.body.style.overflow = 'hidden'; //   
}

//   
function closeMenu() {
    mobileMenu.classList.remove('is-open');
    document.body.style.overflow = ''; //  
}
// ,   
if (openMenuBtn && closeMenuBtn && mobileMenu) {   

    //      
    openMenuBtn.addEventListener('click', openMenu);

    //       
    closeMenuBtn.addEventListener('click', closeMenu);

    //       
    mobileMenuLinks.forEach(link => {
        link.addEventListener('click', () => {
            closeMenu();
        });
    });
}

//    
document.querySelectorAll('a[href^="#"]').forEach(anchor => {
    anchor.addEventListener('click', function (e) {
        e.preventDefault();

        const targetId = this.getAttribute('href');
        const targetElement = document.querySelector(targetId);

        if (targetElement) {
            targetElement.scrollIntoView({
                behavior: 'smooth'
            });
        }
    });
});

// Change theme

const slider = document.querySelector('.switch');

slider.addEventListener('change', changeTheme);

if (localStorage.getItem('darkMode') === 'true') {
    slider.checked = true;
    document.body.classList.add('dark');
}

function changeTheme() {
    if (slider.checked) {
        document.body.classList.add('dark');
        localStorage.setItem('darkMode', 'true');
    } else {
        document.body.classList.remove('dark');
        localStorage.setItem('darkMode', 'false');
    }
}
